      Component.options.__docs = "\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n## Common usage\n\nSet a header and image for the card and provide some content.\n\n```\n  <ds-flex gutter=\"base\" :width=\"{ base: '100%', sm: 1 }\">\n    <ds-flex-item>\n      <ds-card\n        header=\"The header\"\n        image=\"https://picsum.photos/400/300?image=1063\">\n        Look ma! I'm in a card.\n      </ds-card>\n    </ds-flex-item>\n    <ds-flex-item>\n      <ds-card\n        header=\"The header\"\n        image=\"https://picsum.photos/400/300?image=260\">\n        Look ma! I'm in a card.\n      </ds-card>\n    </ds-flex-item>\n  </ds-flex>\n```\n\n## Cards with footer\n\nMost commonly the footer will contain some actions connected to the content.\n\n```\n  <ds-flex gutter=\"base\" :width=\"{ base: '100%', sm: 1 }\">\n    <ds-flex-item>\n      <ds-card\n        header=\"The header\">\n        Look ma! I'm in a card.\n        <template slot=\"footer\">\n          <ds-button color=\"primary\">Read more</ds-button>\n        </template>\n      </ds-card>\n    </ds-flex-item>\n    <ds-flex-item>\n      <ds-card\n        header=\"The header\">\n        Look ma! I'm in a card.\n        <template slot=\"footer\">\n          <ds-button color=\"primary\">Read more</ds-button>\n        </template>\n      </ds-card>\n    </ds-flex-item>\n  </ds-flex>\n```\n"
