export default function (Component) {
      Component.options.__docs = "\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n## Basic usage\n\nDisplay an array of data objects.\n```\n<template>\n  <ds-table :data=\"tableData\">\n  </ds-table>\n</template>\n\n<script>\n  export default {\n    data() {\n      return {\n        tableData: [\n          {\n            name: 'Rengar',\n            type: 'Jungler',\n            loves: 'Hide and seek'\n          },\n          {\n            name: 'Renekton',\n            type: 'Toplaner',\n            loves: 'Slice and dice'\n          },\n          {\n            name: 'Twitch',\n            type: 'ADC',\n            loves: 'Spray and pray'\n          },\n          {\n            name: 'Blitz',\n            type: 'Support',\n            loves: 'Hook you up'\n          }\n        ]\n      }\n    }\n  }\n</script>\n```\n\n## Specify fields\n\nYou can specify which fields to display\n```\n  <template>\n    <ds-table :data=\"tableData\" :fields=\"tableFields\">\n    </ds-table>\n  </template>\n\n  <script>\n    export default {\n      data() {\n        return {\n          tableFields: ['name', 'type'],\n          tableData: [\n            {\n              name: 'Rengar',\n              type: 'Jungler',\n              loves: 'Hide and seek'\n            },\n            {\n              name: 'Renekton',\n              type: 'Toplaner',\n              loves: 'Slice and dice'\n            },\n            {\n              name: 'Twitch',\n              type: 'ADC',\n              loves: 'Spray and pray'\n            },\n            {\n              name: 'Blitz',\n              type: 'Support',\n              loves: 'Hook you up'\n            }\n          ]\n        }\n      }\n    }\n  </script>\n```\n\n## Customize header\n\nYou can customize the header by setting fields as an object.\n\nThe value can be a string representing the fields label or an object with options.\n```\n  <template>\n    <ds-table :data=\"tableData\" :fields=\"tableFields\">\n    </ds-table>\n  </template>\n\n  <script>\n    export default {\n      data() {\n        return {\n          tableFields: {\n            name: 'Hero',\n            type: {\n              label: 'Job',\n              width: '300px'\n            }\n          },\n          tableData: [\n            {\n              name: 'Rengar',\n              type: 'Jungler',\n              loves: 'Hide and seek'\n            },\n            {\n              name: 'Renekton',\n              type: 'Toplaner',\n              loves: 'Slice and dice'\n            },\n            {\n              name: 'Twitch',\n              type: 'ADC',\n              loves: 'Spray and pray'\n            },\n            {\n              name: 'Blitz',\n              type: 'Support',\n              loves: 'Hook you up'\n            }\n          ]\n        }\n      }\n    }\n  </script>\n```\n\n## Custom columns\n\nYou can define custom templates for columns and create columns that do not have a corresponding data attribute.\n\nVia scoped slots you have access to the columns `row`, `index` and `col`.\n```\n<template>\n  <div>\n    <ds-table :data=\"tableData\" :fields=\"tableFields\">\n      <template slot=\"loves\" slot-scope=\"scope\">\n        {{ scope.row.name }} loves {{ scope.row.loves }}\n      </template>\n      <template slot=\"edit\" slot-scope=\"scope\">\n        <ds-button\n          color=\"dark\"\n          size=\"small\"\n          @click=\"deleteRow(scope.row)\">delete</ds-button>\n      </template>\n    </ds-table>\n  </div>\n</template>\n\n<script>\n  export default {\n    data() {\n      return {\n        tableFields: ['name', 'type', 'loves', 'edit'],\n        tableData: [\n          {\n            name: 'Rengar',\n            type: 'Jungler',\n            loves: 'Hide and seek'\n          },\n          {\n            name: 'Renekton',\n            type: 'Toplaner',\n            loves: 'Slice and dice'\n          },\n          {\n            name: 'Twitch',\n            type: 'ADC',\n            loves: 'Spray and pray'\n          },\n          {\n            name: 'Blitz',\n            type: 'Support',\n            loves: 'Hook you up'\n          }\n        ]\n      }\n    },\n    methods: {\n      deleteRow(row) {\n        const index = this.tableData.indexOf(row)\n        if (index > -1) {\n          this.tableData.splice(index, 1)\n        }\n      }\n    }\n  }\n</script>\n```\n"
    }